.custom-button {
    min-width: 165px;
    width: auto;
    height: 50px;
    // letter-spacing: 0.5px;
    line-height: 50px;
    padding: 0 35px 0 35px;
    font-size: 17px;
    background-color: black;
    color: white;
    font-family: 'Akaya Telivigala',
    cursive;
    border: none;
    cursor: pointer;

    &:hover {
        background-color: white;
        color: black;
        border: 1px solid black;
    }

    &.google-sign-in{
        background-color: #4285f4;
        color: white;

        &:hover{
            background-color: #357ae8;
            border: none;
        }
    }
}