body{
    font-family: 'Akaya Telivigala',
    cursive;
    padding: 20px 60px;
}


a{
    text-decoration: none;
}

*{
    box-sizing: border-box;
}
.homepage {
    display: flex;
    /* background-color: aquamarine; */
    flex-direction: column;
    align-items: center;
}
.directory-menu{width:100%;display:flex;flex-wrap:wrap;justify-content:space-between}
.each-item{min-width:30%;height:240px;flex:1 1 auto;display:flex;align-items:center;justify-content:center;border:1px solid #000;margin:0 7.5px 15px;overflow:hidden}.each-item:first-child{margin-right:7.5px}.each-item:last-child{margin-left:7.5px}.each-item .background-image{width:100%;height:100%;background-position:center;background-size:cover}.each-item .content{height:90px;padding:0 25px;display:flex;flex-direction:column;align-items:center;justify-content:center;border:1px solid #000;background-color:#fff;opacity:.7;position:absolute}.each-item .content .title{font-weight:bold;font-size:22px;color:#4a4a4a}.each-item .content .subtitle{font-weight:lighter;padding-bottom:10px;font-size:16px}.large{height:380px}.each-item:hover{cursor:pointer}.each-item:hover .background-image{transform:scale(1.1);transition:transform 6s cubic-bezier(0.2, 0.8, 0.8, 1)}.each-item:hover .content{opacity:.9}

.collection-item{display:flex;flex-direction:column;margin-bottom:30px}.collection-item .title{font-size:28px;margin-bottom:25px}.collection-item .preview{display:flex;justify-content:space-between}
.each-col-item{width:22%;display:flex;flex-direction:column;height:350px;align-items:center}.each-col-item .image{width:100%;height:95%;background-size:cover;background-position:center;margin-bottom:5px}.each-col-item .each-item-footer{width:100%;height:5%;display:flex;justify-content:space-between;font-size:18px}.each-col-item .each-item-footer .name{width:90%;margin-bottom:15px}.each-col-item .each-item-footer .price{width:10%}
.header{height:70px;width:100%;display:flex;justify-content:space-between;margin-bottom:25px}.header .logo-container{width:70px;display:flex;align-items:center;justify-content:flex-start}.header .options{width:50%;height:100%;display:flex;align-items:center;justify-content:flex-end}.header .options .option{padding:10px 15px;cursor:pointer}
.signIn-signUp{width:900px;display:flex;justify-content:space-between;margin:30px auto}
.Signin{width:380px;display:flex;flex-direction:column}.Signin .title{margin:10px}.Signin .buttons{display:flex;justify-content:space-between}
.group{position:relative;margin:45px 0}.group .form-input{background:none;background-color:#fff;color:gray;font-size:18px;padding:10px 10px 10px 5px;display:block;width:100%;border:none;border-radius:0;border-bottom:1px solid gray;margin:25px 0}.group .form-input:focus{outline:none}.group .form-input:focus~.form-input-label{top:-14px;font-size:12px;color:#000}.group input[type=password]{letter-spacing:.3em}.group .form-input-label{color:gray;font-size:16px;font-weight:normal;position:absolute;pointer-events:none;left:5px;top:10px;transition:300ms ease all}.group .form-input-label.shrink{top:-14px;font-size:12px;color:#000}
.custom-button{min-width:165px;width:auto;height:50px;line-height:50px;padding:0 35px 0 35px;font-size:17px;background-color:#000;color:#fff;font-family:"Akaya Telivigala",cursive;border:none;cursor:pointer}.custom-button:hover{background-color:#fff;color:#000;border:1px solid #000}.custom-button.google-sign-in{background-color:#4285f4;color:#fff}.custom-button.google-sign-in:hover{background-color:#357ae8;border:none}
.sign-up{display:flex;flex-direction:column;width:380px}.sign-up .title{margin:10px 0}
